// å·¦ä¾§ï¼šæŠ˜å ä¸é€‰ä¸­çŠ¶æ€
const faqOpen = ref(true)
type Category = 'faq' | 'guide' | 'vendor' | 'biz'
const selectedCategory = ref<Category>('faq')
const faqGroups = ['è´¦å·ä¸ç™»å½•', 'åˆ›å»ºå½¢è±¡', 'çŸ¥è¯†åº“', 'åˆ†äº«', 'å•†åŸ', 'å…¶ä»–']
